import React,{
  useState,useEffect,
  useCallback} from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import {
  Box,
  Button,
  Card,
  CardContent,
  makeStyles
} from '@material-ui/core';
import wait from 'src/utils/wait';
import FilesDropzone from 'src/components/FilesDropzone';
import useAuth from 'src/hooks/useAuth';
import {uploadExcel,has_running_command} from '../../../api/schools.js';
import { useHistory } from 'react-router-dom';
import useIsMountedRef from 'src/hooks/useIsMountedRef';

const useStyles = makeStyles(() => ({
  root: {}
}));

const UploadForm = ({
  className,
  ...rest
}) => {
  const history = useHistory();

  const school_id = localStorage.getItem('schoolid');
  //check status
  const [command, setCommand] = useState(null);
  
  const isMountedRef = useIsMountedRef();
  const getCommand = useCallback(async () => {
    try {
      const command = await has_running_command(school_id);
    console.log(command);
      if (isMountedRef.current) {
        setCommand(command);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {
    getCommand();
  }, [getCommand]);
    
   

  const [files, setFiles] = useState([]);
  // let school = JSON.parse(localStorage.getItem('school'));
  const { user} = useAuth();
  // console.log(school)
  console.log(user.name)
  
  const handleRemoveAll = () => {
    setFiles([]);
  };
  const handleDrop = useCallback((acceptedFiles) => {
    setFiles((prevFiles) => acceptedFiles);
  }, []);
  const classes = useStyles();
  const { enqueueSnackbar } = useSnackbar();
  if(command!=null && command.length>0){
    console.log(command)
    return (
      <div>
        <h1 >Running</h1>
      {command.map((c) => {
                
                return (
                  <h5 key={c.id}>{c.data.school_id}</h5>
                );
              })}
      </div>
    );

  } else
    return (
    <Formik
    initialValues={{
      submit: null
    }}
      onSubmit={async (values, {
        resetForm,
        setErrors,
        setStatus,
        setSubmitting
      }) => {
        try {
          console.log("click save")

          fetch(`https://geolocation-db.com/json/`).then(res=>res.json()).then( async (result)=>{
            if(typeof(user.name)=="undefined"){
              result.name = user.firstname + " " + user.lastname;  
            } else {
              result.name=user.name
            }
            result.userid=user.userId
            result.created_at = new Date().getTime();
            const resultUp = await uploadExcel(result, files[0],school_id);
            if(resultUp=="success"){
              await wait(500);
              resetForm();
              setStatus({ success: true });
              setSubmitting(false);
              enqueueSnackbar('Bắt đầu chạy đồng bộ', {
                variant: 'success',
                action: <Button>start</Button>
              });
              await wait(500);
              history.push('/school/management/classes');
            } else {
              // alert(resultUp);
              await wait(500);
              resetForm();
              setStatus({ success: true });
              setSubmitting(false);
              enqueueSnackbar(resultUp, {
                variant: 'resultUp',
                action: <Button>start</Button>
              });
              await wait(500);
              history.push('/school/management/classes');
              
            }
          });
          
          // setSchool(values)
          // console.log(values)
          // NOTE: Make API request
          

        } catch (err) {
          console.error(err);
          setStatus({ success: false });
          setErrors({ submit: err.message });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        touched,
        values
      }) => (
        <form
          className={clsx(classes.root, className)}
          onSubmit={handleSubmit}
          {...rest}
        >
          <Card>
            <CardContent>
            <CardContent>
              <FilesDropzone handleDrop = {handleDrop} files={files} handleRemoveAll={handleRemoveAll}/>
            </CardContent>
              <Box mt={2}>
                <Button
                  variant="contained"
                  color="secondary"
                  type="submit"
                  disabled={isSubmitting}
                >
                  Lưu
                </Button>
              </Box>
            </CardContent>
          </Card>
        </form>
      )}
    </Formik>
  );
};

UploadForm.propTypes = {
  className: PropTypes.string
};

export default UploadForm;
